import '../components/tables.js';
import '../components/resources.js';
import '../components/sprite.js';
import '../components/animator.js';
import '../components/input.js';
import '../components/renderer.js';
import '../components/collision.js';
import  '../components/events.js';
import '../components/vector.js';
import LevelManager from '../managers/LevelManager';
import Player from '../models/Player';
import {Enemy, Explosion} from '../components/entities';


Renderer.createCanvas();

var ctx = Renderer.settings.ctx;
var canvas = Renderer.settings.canvas;

var requestAnimFrame = window.requestAnimationFrame;
var framePerSec =30;
var frameTime = 1/framePerSec;
var tempTime = 0;
// The main game loop
var lastTime;
events.on('updateEachFrame', update);
function loop() {
    var now = Date.now();
    var dt = (now - lastTime) / 1000.0;
    if(tempTime<frameTime){
        tempTime+= dt;
    } else if(tempTime>=frameTime){
        events.trigger('renderEachFrame',tempTime);
        events.trigger('updateEachFrame',tempTime,entities);
        Renderer.renderAll(isGameOver, entities, tempTime);
        tempTime=0
    }
    lastTime = now;
    requestAnimFrame(loop);
};

function init() {
    Renderer.createTerrain('img/terrain.jpg','no-repeat');
    document.getElementById('play-again').addEventListener('click', function() {  LevelManager.restart(); });

    lastTime = Date.now();
    loop();
};

resources.load([
    'img/entities.png',
    'img/attacks.png',
    'img/explosions.png',
    'img/terrain.jpg'
]);

document.getElementById('play').addEventListener('click', function() { 
    if(resources.isReady())
        init(); 
        LevelManager.start(); 
});

window.entities = [];

var enemy = new Enemy();
var player = new Player();
var ex1 = new Explosion([410,320],0);
var ex2 = new Explosion([140,320],1);
var ex3 = new Explosion([220,320],2);
var ex4 = new Explosion([350,320],3);

var gameTime = LevelManager.settings.gameTime;
var isGameOver = LevelManager.settings.isGameOver;


// Update game objects
function update(e, dt, entities) {
    gameTime += dt;
    handleInput(dt);
    updateEntities(dt, entities);
    Collision.checkCollisions(entities);
    LevelManager.displayScore();
};

