export class Monster extends Entity {
    constructor (pos) {
        super();
            this.pos = pos||[440,340];
            this.sprN = Math.round(Math.random()*2);
            this.b = Table.monsters;
                                // ajustment      framePos                 frameSize in sprite sheet
            this.lArm = new LArm(   [-24,1],      [240,80*this.sprIndex],[60,80], this);
            this.legs = new Legs(   [-22,23],      [360,80*this.sprIndex],[60,80], this);
            this.body = new Body(  [-13,-3],      [180,80*this.sprIndex],[60,80], this);
            this.rArm = new RArm(   [4,1],        [300,80*this.sprIndex],[60,80], this);
            this.head = new Head(   [-30,-68],    [0,80*this.sprIndex],[60,80], this);
    }
}

export  class Enemy extends Monster {
    constructor(pos,sprite) {
        super(pos);
        this.name = 'enemy';
        this.sprite = null;
        this.bodyMT =0.4;
        this.idleAnim = new Animation('idle',this.idle());
        // this.attackAnim = new Animation();
        this.animator = new Animator([this.idleAnim], 'idle');
    }
    idle(){
         // this.head, tempProgress,  max, direction, time, repStyle(once,repeat,bounce), [sprite pos, sprite frames, sprite speed]
        let name = 'idle',
            st = 'bounce',
            names = ['head','body','lArm','rArm','legs'];
        return [[this[names[0]], 0, 1 , [-1,1], this.bodyMT, st,1,  [this.b[name][names[0]][this.sprN].sp,this.b[name][names[0]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[1]], 0, 1, [0,1], this.bodyMT,st,1,     [this.b[name][names[1]][this.sprN].sp,this.b[name][names[1]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[2]], 0, 1, [-1,1], this.bodyMT, st,1,   [this.b[name][names[2]][this.sprN].sp,this.b[name][names[2]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[3]], 0, 1, [1,1], this.bodyMT, st,1,    [this.b[name][names[3]][this.sprN].sp,this.b[name][names[3]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[4]], 0, 1, [1,0], this.bodyMT,st,1,     [this.b[name][names[4]][this.sprN].sp,this.b[name][names[4]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]]];
    }
    update(e,dt,entities){
        // this.translate([-0.5,0]);
    }
}

export class Explosion extends Entity {
    constructor(pos,explIndex){
            super();
            this.pos = pos||[440,340];
            this.name = 'explosion';
            this.sprN = explIndex;
            this.b = Table.explosions;
                                                    // url, pos, size, speed, frames, dir, once
            this.sprite =  new Sprite('img/explosions.png', this.b.idle[this.sprN].sp, 
                                                            this.b.idle[this.sprN].frsize, 
                                                            this.b.idle[this.sprN].speed, 
                                                            this.b.idle[this.sprN].frs,
                                                            null,true);
            this.idleAnim = new Animation('idle',this.idle());
            this.explodeAnim = new Animation('explode',this.explode());
            this.animator = new Animator([this.idleAnim,this.explodeAnim], 'idle');
       }
       idle(){
        // this.head, tempProgress,  max, direction, time, repStyle
       let name = 'idle',st = 'bounce',names = ['explosions'];
       return [[this, 0, 1 , [-1,1], 1, st,1,  [  this.b[name][this.sprN].sp,
                                                  this.b[name][this.sprN].frs,
                                                  this.b[name][this.sprN].speed,
                                                  this.b[name][this.sprN].once]  ],
               ];
    }
    explode(){
        // this.head, tempProgress,  max, direction, time, repStyle
       let name = 'explode',st = 'bounce',names = ['explosions'];
       return [[this, 0, 1 , [-1,1], 1, st,1,  [  this.b[name][this.sprN].sp,
                                                  this.b[name][this.sprN].frs,
                                                  this.b[name][this.sprN].speed,
                                                  this.b[name][this.sprN].once]  ],
               ];
     }
     update(e,dt){
         
         if(input.isDown('space')){
            this.animator.setAnimation('explode',true);
        }
     };
}