import {Entity, BodyParticle, Body, Head, LArm, RArm, Legs } from '../components/entities';

export class Humanoid extends Entity {
    constructor (pos) {
        super();
            this.pos = pos||[160,320];
            this.sprN=0;
            this.b = Table.humanoids;
                                //  ajustment      framePos frameSize in sprite sheet
            this.rArm = new RArm( [3,1], this.b.idle.rArm[this.sprN].sp,this.b.idle.rArm[this.sprN].frsize, this);
            this.legs = new Legs( [-20,24], this.b.idle.legs[this.sprN].sp,this.b.idle.legs[this.sprN].frsize, this);
            this.body = new Body( [-13,-3], this.b.idle.body[this.sprN].sp,this.b.idle.body[this.sprN].frsize, this);
            this.lArm = new LArm( [-17,1], this.b.idle.lArm[this.sprN].sp,this.b.idle.lArm[this.sprN].frsize, this);
            this.head = new Head( [-30,-73], this.b.idle.head[this.sprN].sp,this.b.idle.head[this.sprN].frsize, this);
            
    }
}

class Player extends Humanoid{
    constructor(pos,sprite) {
        super(pos);
        this.name = 'player';
        this.sprite = null;
        this.bodyMT =0.9;
    
        this.idleAnim = new Animation('idle',this.idle());
         this.attackAnim = new Animation('attack',this.attack());
        this.animator = new Animator([this.idleAnim, this.attackAnim], 'idle');
    }
    idle(){
        let name = 'idle',
            st = 'bounce',
            names = ['head','body','lArm','rArm','legs'];
            return [[this[names[0]], 0, 1 , [-1,1], this.bodyMT, st,1,  [this.b[name][names[0]][this.sprN].sp,this.b[name][names[0]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[1]], 0, 1, [0,1], this.bodyMT,st,1,     [this.b[name][names[1]][this.sprN].sp,this.b[name][names[1]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[2]], 0, 1, [-1,1], this.bodyMT, st,1,   [this.b[name][names[2]][this.sprN].sp,this.b[name][names[2]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[3]], 0, 1, [1,1], this.bodyMT, st,1,    [this.b[name][names[3]][this.sprN].sp,this.b[name][names[3]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                [this[names[4]], 0, 1, [1,0], this.bodyMT,st,1,     [this.b[name][names[4]][this.sprN].sp,this.b[name][names[4]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]]];
    }
    attack(){
        // this.head, tempProgress,  max, direction, time, repStyle(once,repeat,bounce), [sprite pos, sprite frames, sprite speed, once]
        let name = 'attack',
            st = 'bounce',
            names = ['head','body','lArm','rArm','legs'];
            return [[this[names[0]], 0, 2 , [-1,4], this.bodyMT, st,1,  [this.b[name][names[0]][this.sprN].sp,this.b[name][names[0]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                    [this[names[1]], 0, 5, [0,1], this.bodyMT,st,1,     [this.b[name][names[1]][this.sprN].sp,this.b[name][names[1]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                    [this[names[2]], 0, 5, [-1,1], this.bodyMT, st,1,   [this.b[name][names[2]][this.sprN].sp,this.b[name][names[2]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                    [this[names[3]], 0, 4 , [1,1], this.bodyMT, st,1,    [this.b[name][names[3]][this.sprN].sp,this.b[name][names[3]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]],
                    [this[names[4]], 0, 4, [1,0], this.bodyMT,st,1,     [this.b[name][names[4]][this.sprN].sp,this.b[name][names[4]][this.sprN].frs,this.b[name][names[0]][this.sprN].speed,this.b[name][names[0]][this.sprN].once]]];
    }
    update(e,dt,entities){
        if(input.isDown('space')){
            this.animator.setAnimation('attack',true);
        }
    }
}

export {Player as default} 